<template>
  <div id="app">
    <div id="mainList" class="grey lighten-3">
      <v-layout row wrap>
        <v-flex xs12>
          <v-app id="shoppingList">
            <v-card>
              <v-toolbar color="blue-grey darken-2">
                <!--<v-toolbar-side-icon/>-->
                <v-toolbar-title class="white--text">Ultimate Shopping List</v-toolbar-title>
                <v-spacer/>
                <v-btn icon @click="add_new_item">
                  <v-icon>add</v-icon>
                </v-btn>
                <!--<v-btn icon>-->
                  <!--<v-icon>search</v-icon>-->
                <!--</v-btn>-->
              </v-toolbar>
              <v-list>
                  <draggable class="draggable_list" v-model="items" :options="{handle:'.drag_item'}" @start="drag=true" @end="drag=false" @change="updateList">
                    <shopping_item :items="items" :index="index" :edit.sync="item.edit" :order="item.order" :key="item.order" :name.sync="item.name" :done.sync="item.done" v-for="(item,index) in items"/>
                  </draggable>
              </v-list>
            </v-card>
          </v-app>
        </v-flex>
      </v-layout>
    </div>
  </div>
</template>

<script>
import draggable from 'vuedraggable'

export default {
  name: 'App',
  components: {
    draggable,
    'shopping_item': {
      template: `
            <div>
              <v-list-tile color="blue-grey lighten-4">
                <v-list-tile-action>
                  <v-checkbox v-model="done" @click="mark_as_done" :validate-on-blur="true"></v-checkbox>
                </v-list-tile-action>
                <v-list-tile-content v-if="!edit" @click="edit_item">
                  <span class="item_text" v-bind:class="{ item_done : done }">{{ name }}</span>
                </v-list-tile-content>
                <v-list-tile-content v-else>
                  <v-text-field single-line class="edit_item_name" v-on:keyup.enter="save_item" v-focus-on-create v-bind:value="name" ref="new_name" :hide-details="true"></v-text-field>
                </v-list-tile-content>
                <v-list-tile-action v-if="edit">
                  <v-icon color="green" @click="save_item" dark>save</v-icon>
                </v-list-tile-action>
                <v-list-tile-action v-if="edit">
                  <v-icon color="red" @click="close_edit_item" dark>close</v-icon>
                </v-list-tile-action>
                <v-spacer/>
                <v-list-tile-action class="drag_item"><v-icon class="drag_icon" right>reorder</v-icon></v-list-tile-action>
              </v-list-tile>
              <v-divider v-if="index + 1 < items.length"/>
            </div>`,
      props: ['order', 'name', 'done', 'edit', 'index', 'items'],
      methods: {
        mark_as_done: function () {
          this.$emit('update:done', !this.done)
        },
        edit_item: function () {
          // this.edit = true
          // hide all edits then open this one
          this.$root.$emit('close_edit')
          this.$emit('update:edit', true)
        },
        save_item: function () {
          this.$emit('update:name', this.$refs['new_name'].inputValue)
          this.$root.$emit('close_edit')
        },
        close_edit_item: function () {
          this.$root.$emit('close_edit')
        }
      }
    }
  },
  mounted: function () {
    this.$root.$on('close_edit', () => {
      let self = this
      this.items.forEach(function (value, key) {
        value.edit = false
        if (value.name.trim() === '') {
          console.log(key)
          console.log(self.items)
          self.items.splice(key, 1)
          console.log(self.items)
        }
      })
    })
    this.$root.$on('change', () => {
      console.log('moved')
    })
  },
  methods: {
    add_new_item: function () {
      let newOrder = this.items.length
      let item = {
        order: newOrder,
        name: '',
        done: false,
        edit: true
      }
      this.$root.$emit('close_edit')
      this.items.push(item)
    },
    updateList: function (data) {
      this.items.forEach(function (value, key) {
        // Update order based on key
        value.order = key
      })
    }
  },
  data () {
    return {
      items: [
        {order: 0,
          name: 'Bread',
          done: false,
          edit: false},
        {order: 1,
          name: 'Milk',
          done: true,
          edit: false}
      ]
    }
  }
}
</script>

<style>
  #mainList {
    max-width:400px;
    margin: auto;
  }
  #shoppingList .edit_item_name.input-group {
    padding-top: 0;
  }
  #shoppingList .edit_item_name .input-group__input {
    padding-left: 0;
  }
  #shoppingList .edit_item_name input {
    padding-top: 17px;
  }
  .shopping_item .card__text {
    padding-top: 0;
    padding-bottom: 5px;
    padding-left: 2px;
    padding-right: 2px;
    cursor: pointer;
  }
  #shoppingList .shopping_item {
    padding: 2px;
  }
  .draggable_list {
    width: 100%;
  }
  .drag_icon {
    cursor: move;
    float: right;
  }
  .item_done {
    text-decoration: line-through;
  }
</style>



